<template>
  <v-content>
    <Cabecera />
    <v-container v-if="mostrar == 'cursos'">
      <div v-if="loading_cursos" class="loading-cursos text-center">
        <h3 class="mb-5">Cargando Cursos</h3>
        <v-progress-circular :size="50" color="#1976d2" indeterminate></v-progress-circular>
      </div>
      <div
        v-else
        class="my-6 elevation-2"
        v-for="(curso, c_idx) in cursos"
        :key="c_idx"
        style="background: #fff"
      >
        <div class="d-flex px-6 py-4">
          <span class="curso-nombre">{{curso.nombre}}</span>
          <div class="flex-grow-1"></div>
          <v-btn
            class="mr-3"
            v-if="sesion['tipo'] == 'profesor'"
            rounded
            color="success"
            dark
            :loading="loading_alumnos"
            @click="verAlumnos(curso.curso_id)"
          >
            Matriculados
            <v-icon class="ml-3">mdi-account-group</v-icon>
          </v-btn>
          <v-btn
            :loading="curso['loading_posiciones']"
            rounded
            color="warning"
            dark
            @click="verScoreboard(curso)"
          >
            Posiciones
            <v-icon class="ml-3">mdi-trophy</v-icon>
          </v-btn>
        </div>
        <v-divider></v-divider>
        <div class="px-4 py-3">
          <v-btn
            class="ma-1"
            v-for="(tema, t_idx) in curso.temas"
            :key="t_idx"
            rounded
            color="primary"
            dark
            @click="ingresarCreacion(tema.tema_id)"
          >{{tema.nombre}}</v-btn>
          <v-btn v-if="sesion.tipo == 'profesor'" class="ma-1" rounded outlined color="grey">
            <v-icon>mdi-plus</v-icon>
          </v-btn>
        </div>
      </div>
    </v-container>
    <v-container v-else-if="mostrar == 'alumnos'">
      <div class="elevation-6">
        <v-toolbar flat>
          <v-btn icon @click="mostrar = 'cursos'">
            <v-icon>mdi-arrow-left</v-icon>
          </v-btn>
          <h3 class="ml-2">Alumnos</h3>
          <div class="flex-grow-1"></div>
          <div class="text-center">
            <v-dialog v-model="dialogAgregarAlumno" width="600">
              <template v-slot:activator="{ on }">
                <v-btn rounded color="success" dark v-on="on">
                  Agregar Alumno
                  <v-icon class="ml-3">mdi-account-plus</v-icon>
                </v-btn>
              </template>

              <v-card class="buscar-alumno">
                <v-card-title class="headline grey lighten-2" primary-title>
                  <form
                    style="width: 100%; display: flex; align-items: center"
                    @submit.prevent="buscarAlumno()"
                  >
                    <v-text-field
                      v-model="alumnoBuscar"
                      label="Nombre o Apellido"
                      single-line
                      clearable
                    ></v-text-field>
                    <v-btn class="ml-4" fab text small @click="buscarAlumno()">
                      <v-icon>mdi-magnify</v-icon>
                    </v-btn>
                  </form>
                </v-card-title>
                <div class="contenedor-encontrados">
                  <v-card-text v-if="alumnos_encontrados.length > 0" class="py-1">
                    <v-card
                      class="d-flex justify-space-between align-center my-3"
                      v-for="(a, a_idx) in alumnos_encontrados"
                      :key="a_idx"
                    >
                      <div class="pa-4">
                        {{a.nombre}}
                        {{a.apellido_paterno}}
                        {{a.apellido_materno}}
                      </div>
                      <v-btn
                        :loading="agregando"
                        color="success"
                        text
                        icon
                        small
                        @click="dialog_matricular = true; alumno_seleccionado = a"
                        class="mr-4"
                      >
                        <v-icon>mdi-plus</v-icon>
                      </v-btn>
                    </v-card>
                  </v-card-text>
                  <v-card-text v-else class="text-center pt-5">No hay alumnos encontrados.</v-card-text>
                </div>
              </v-card>
            </v-dialog>
          </div>
        </v-toolbar>
        <v-simple-table>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">Código</th>
                <th class="text-left">Nombre</th>
                <th class="text-left">Apellido Materno</th>
                <th class="text-left">Apellido Paterno</th>
                <th class="text-left"></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(alumno, a_idx) in alumnos" :key="a_idx">
                <td>{{ alumno.usuario }}</td>
                <td>{{ alumno.nombre }}</td>
                <td>{{ alumno.apellido_paterno }}</td>
                <td>{{ alumno.apellido_materno }}</td>
                <td>
                  <v-btn
                    class="mx-2"
                    fab
                    text
                    dark
                    small
                    color="error"
                    @click="alumno_seleccionado = alumno; dialog_desmatricular = true"
                  >
                    <v-icon dark>mdi-delete</v-icon>
                  </v-btn>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </div>
    </v-container>
    <v-container v-else-if="mostrar == 'posiciones'">
      <div style="background: #fff" class="elevation-6">
        <v-toolbar flat>
          <v-btn icon @click="mostrar = 'cursos'">
            <v-icon>mdi-arrow-left</v-icon>
          </v-btn>
          <h3 class="ml-2">Posiciones</h3>
        </v-toolbar>
        <div class="contenedor-posiciones">
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">Alumno</th>
                  <th class="text-left">Puntaje</th>
                  <th class="text-left">Gráficos</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(a, a_idx) in alumnos" :key="a_idx">
                  <td>
                    <span>{{a_idx + 1}}.</span>
                    {{`${a['nombre']} ${a['apellido_paterno']} ${a['apellido_materno']}`}}
                  </td>
                  <td>{{a['puntaje_total']}}</td>
                  <td class="py-1">
                    <v-btn
                      fab
                      x-small
                      class="mx-1 my-1"
                      color="primary"
                      @click="verDashboardEvaluacion(a['evaluaciones'])"
                    >
                      <v-icon dark>mdi-format-list-checks</v-icon>
                    </v-btn>
                    <v-btn
                      :loading="a['loading_dashboard_tiempo']"
                      fab
                      x-small
                      class="mx-1 my-1"
                      color="success"
                      @click="verDashboardTiempo(a)"
                    >
                      <v-icon dark>mdi-clock-outline</v-icon>
                    </v-btn>
                    <!-- <v-icon class="ml-2" v-if="a_idx == 0" color="#FFD700">mdi-podium-gold</v-icon>
                    <v-icon class="ml-2" v-else-if="a_idx == 1" color="#C0C0C0">mdi-podium-silver</v-icon>
                    <v-icon class="ml-2" v-else-if="a_idx == 2" color="#cd7f32">mdi-podium-bronze</v-icon>-->
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </div>
      </div>
    </v-container>

    <!-- Dialog Matricular -->
    <v-dialog v-model="dialog_matricular" max-width="290">
      <v-card>
        <v-card class="text-center">
          <v-card-text style="font-size: 16px">¿Desea agregar al alumno seleccionado?</v-card-text>
          <v-card-actions>
            <div class="flex-grow-1"></div>
            <v-btn
              text
              @click="dialog_matricular = false; matricularAlumno()"
              color="primary"
            >Agregar</v-btn>
            <v-btn text @click="dialog_matricular = false">Cancelar</v-btn>
            <div class="flex-grow-1"></div>
          </v-card-actions>
        </v-card>
      </v-card>
    </v-dialog>
    <!-- Dialog Desmatricular -->
    <v-dialog v-model="dialog_desmatricular" max-width="290">
      <v-card>
        <v-card class="text-center">
          <v-card-text style="font-size: 16px">¿Desea eliminar al alumno seleccionado?</v-card-text>
          <v-card-actions>
            <div class="flex-grow-1"></div>
            <v-btn
              text
              @click="dialog_desmatricular = false; desmatricularAlumno()"
              color="error"
            >Eliminar</v-btn>
            <v-btn text @click="dialog_desmatricular = false">Cancelar</v-btn>
            <div class="flex-grow-1"></div>
          </v-card-actions>
        </v-card>
      </v-card>
    </v-dialog>
    <!-- Dialog Dashboard -->
    <v-dialog v-model="dialog_dashboard" max-width="600">
      <v-card>
        <canvas id="myChart" width="600" height="400"></canvas>
      </v-card>
    </v-dialog>
  </v-content>
</template>

<script>
import Chart from "chart.js";
import Cabecera from "../components/Cabecera";
import Alumno from "../models/Alumno";
import Curso from "../models/Curso";

export default {
  data() {
    return {
      sesion: null,
      alumnoBuscar: "",
      mostrar: "cursos",
      alumnos: [],
      cursos: [],
      curso_id_seleccionado: null,
      alumnos_encontrados: [],
      alumno_seleccionado: null,
      agregando: false,
      myChart: null,
      // Dialog
      dialogAgregarAlumno: false,
      dialog_matricular: false,
      dialog_desmatricular: false,
      dialog_dashboard: false,
      // Progress
      loading_cursos: true,
      loading_alumnos: false
    };
  },
  mounted() {
    this.sesion = this.$store.state.servicio.sesion.obtenerSesion();
    let obtenerCurso =
      this.sesion.tipo == "profesor"
        ? this.$store.state.servicio.curso.obtenerCursoPorProfesor
        : this.$store.state.servicio.curso.obtenerCursoPorAlumno;

    obtenerCurso(
      this.sesion["id"],
      response => {
        this.loading_cursos = false;
        let cursos = JSON.parse(response);
        cursos.forEach(c => {
          let curso = new Curso(c._id.$oid, c.nombre);
          curso["loading_posiciones"] = false;
          this.cursos.push(curso);

          this.$store.state.servicio.tema.obtenerTemaPorCurso(
            curso.curso_id,
            response => {
              let temas = JSON.parse(response);
              temas.forEach(t => {
                curso.temas.push(new Tema(t._id.$oid, t.nombre));
              });
            },
            error => {
              console.log(error);
            }
          );
        });
      },
      error => {
        this.loading_cursos = false;
        console.log(error);
      }
    );
  },
  methods: {
    buscarAlumno() {
      if (this.alumnoBuscar) {
        this.$store.state.servicio.alumno.obtenerAlumnoPorNombre(
          this.alumnoBuscar,
          response => {
            this.alumnos_encontrados = [];
            JSON.parse(response).forEach(res => {
              let agregar = true;
              this.alumnos.forEach(a => {
                if (res._id.$oid == a.alumno_id) {
                  agregar = false;
                }
              });
              if (agregar) {
                this.alumnos_encontrados.push(
                  new Alumno(
                    res._id.$oid,
                    res["codigo"],
                    res["nombre"],
                    res["apellido_paterno"],
                    res["apellido_materno"]
                  )
                );
              }
            });
          },
          error => {
            console.log(error);
          }
        );
      }
    },
    verScoreboard(curso) {
      let curso_id = curso["curso_id"];
      curso["loading_posiciones"] = true;
      this.alumnos = [];
      let promises = [];
      let alumnos_aux = [];

      this.$store.state.servicio.alumno.obtenerAlumnosPorCurso(
        curso_id,
        response => {
          response.alumnos.forEach(a => {
            promises.push(
              new Promise((res, rej) => {
                this.$store.state.servicio.alumno.obtenerAlumnoPorId(
                  a,
                  response => {
                    let alumno = new Alumno(
                      response["alumno_id"],
                      response["codigo"],
                      response["nombre"],
                      response["apellido_paterno"],
                      response["apellido_materno"]
                    );
                    alumno["loading_dashboard_tiempo"] = false;
                    alumnos_aux.push(alumno);
                    this.$store.state.servicio.evaluacion.puntajeTotalAlumnoPorCurso(
                      alumno["alumno_id"],
                      curso_id,
                      response => {
                        alumno["evaluaciones"] = response;
                        alumno["puntaje_total"] = response
                          .map(e => e["puntajeTotal"])
                          .reduce(function(a, b) {
                            return a + b;
                          }, 0);
                        res();
                      },
                      error => {
                        console.log(error);
                        rej();
                      }
                    );
                  },
                  error => {
                    console.log(error);
                    rej();
                  }
                );
              })
            );
          });

          Promise.all(promises).then(res => {
            curso["loading_posiciones"] = false;
            this.mostrar = "posiciones";
            this.ordenarPosiciones(alumnos_aux);
          });
        },
        error => {
          console.log(error);
        }
      );
    },
    ordenarPosiciones(alumnos_aux) {
      alumnos_aux.sort(function(a, b) {
        return b["puntaje_total"] - a["puntaje_total"];
      });
      this.alumnos = alumnos_aux;
    },
    verAlumnos(curso_id) {
      this.curso_id_seleccionado = curso_id;
      this.loading_alumnos = true;
      this.$store.state.servicio.alumno.obtenerAlumnosPorCurso(
        curso_id,
        response => {
          this.loading_alumnos = false;
          this.mostrar = "alumnos";
          this.alumnos = [];

          response.alumnos.forEach(a => {
            this.$store.state.servicio.alumno.obtenerAlumnoPorId(
              a,
              response => {
                let alumno = new Alumno(
                  response["alumno_id"],
                  response["codigo"],
                  response["nombre"],
                  response["apellido_paterno"],
                  response["apellido_materno"]
                );
                this.alumnos.push(alumno);
              },
              error => {
                console.log(error);
              }
            );
          });
        },
        error => {
          console.log(error);
        }
      );
    },
    ingresarCreacion(tema_id) {
      if (this.sesion.tipo == "profesor")
        this.$router.push({ name: "creacion", params: { tema_id: tema_id } });
      else this.$router.push({ name: "chatbot", params: { tema_id: tema_id } });
    },
    matricularAlumno() {
      let alumno_clone = JSON.parse(JSON.stringify(this.alumno_seleccionado));
      this.$store.state.servicio.matricula.matricularAlumno(
        alumno_clone.alumno_id,
        this.curso_id_seleccionado,
        res => {
          this.alumnos_encontrados.forEach((ae, ae_idx) => {
            if (ae.alumno_id == alumno_clone.alumno_id) {
              this.alumnos_encontrados.splice(ae_idx, 1);
            }
          });
          this.alumnos.push(alumno_clone);
        },
        err => {
          console.log(err);
        }
      );
    },
    desmatricularAlumno() {
      let alumno_id = this.alumno_seleccionado.alumno_id;
      this.$store.state.servicio.matricula.desmatricularAlumno(
        alumno_id,
        this.curso_id_seleccionado,
        res => {
          this.alumnos.forEach((a, a_idx) => {
            if (a.alumno_id == alumno_id) {
              this.alumnos.splice(a_idx, 1);
            }
          });
        },
        err => {
          console.log(err);
        }
      );
    },
    verDashboardTiempo(alumno) {
      alumno["loading_dashboard_tiempo"] = true;
      this.$store.state.servicio.tiempo.tiempoTotalAlumnoPorDia(
        alumno["alumno_id"],
        response => {
          alumno["loading_dashboard_tiempo"] = false;
          let tiempos = response;
          tiempos.forEach(t => {
            t["tiempoTotal"] = Math.round(t["tiempoTotal"] / 60000);
          });
          this.verDashboard(
            tiempos,
            "fecha",
            "tiempoTotal",
            "Tiempo(min) ingresado de los últimos 5 días.",
            "#4caf50"
          );
        }
      );
    },
    verDashboardEvaluacion(evaluaciones) {
      this.verDashboard(
        evaluaciones,
        "fecha",
        "puntajeTotal",
        "Evaluación de los últimos 5 días.",
        "#1976d2"
      );
    },
    verDashboard(array, label, value, title, color) {
      if (this.myChart) this.myChart.destroy();
      this.dialog_dashboard = true;
      setTimeout(() => {
        var ctx = document.getElementById("myChart").getContext("2d");
        this.myChart = new Chart(ctx, {
          type: "line",
          data: {
            labels: array.map(item => item[label]),
            datasets: [
              {
                label: title,
                data: array.map(item => item[value]),
                backgroundColor: [color + "50"],
                borderColor: [color],
                borderWidth: 1
              }
            ]
          },
          options: {
            scales: {
              yAxes: [
                {
                  ticks: {
                    beginAtZero: true,
                    stepSize: 1
                  }
                }
              ]
            }
          }
        });
      }, 200);
    }
  },
  components: {
    Cabecera
  }
};
class Tema {
  constructor(tema_id, nombre) {
    this.tema_id = tema_id;
    this.nombre = nombre;
  }
}
</script>

<style scoped>
.curso-nombre {
  display: block;
  font-size: 24px;
}
.tema-docente {
  color: #525252;
  font-size: 16px;
  margin-bottom: 0;
  padding: 0 10px;
}
.buscar-alumno {
  min-height: 400px;
}
.contenedor-encontrados {
  height: 360px;
  overflow: auto;
}
.contenedor-posiciones {
  max-width: 750px;
  margin: 0 auto;
  padding: 20px 0;
}
.loading-cursos {
  padding: 40px;
}
</style>
